<template>
  <div class="page">
    <div class="content">
      <ul class="articles">
        <li>
          <div class="time">{{info.time}}</div>
          <div class="title">{{info.title}}</div>
          <div class="ms" v-html="info.nr"></div>
        </li>
        <!-- 来必力City版安装代码 -->
        <div id="lv-container" data-id="city" data-uid="MTAyMC81Nzc1NC8zNDIxNw==">
        </div>
        <!-- City版安装代码已完成 -->
<!--        <vssue :key="$route.params.id"/>-->
      </ul>
      <transition name="mys">
        <div class="mark" v-if="expup" @click="gb"></div>
      </transition>
      <ul class="listall" :class="{expleft:expleft&&ispc,expup:expup&&!ispc}">
        <h2>文章列表<i class="iconfont icon-close" @click="gb"></i></h2>
        <router-link tag="li" :to="'/lifes/'+item.id" v-for="(item,xh) in articles" :key="xh">{{item.title}}
        </router-link>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
    name: 'articles',
    data() {
        return {
            info: {},
            articles: [],
            ispc: true
        }
    },
    props: {
        expleft: {
            type: Boolean,
            default: true //默认default
        },
        expup: {
            type: Boolean,
            default: false //默认default
        },
    },
    created() {
        fetch('https://www.fastmock.site/mock/5f854ec44ed74b246903c6e75806ec14/api/life').then((rs) => {
            return rs.json()
        }).then((data) => {
            this.articles = data
            let lh = document.querySelector('.listall').offsetHeight
            let ah = this.articles.length * 24
            if (ah > lh) {
                document.querySelector('.listall').style.cssText = '--gd:0'
            } else {
                document.querySelector('.listall').style.cssText = '--gd:15'
            }
        })
    },
    mounted() {
        let sw = window.innerWidth
        let _this = this
        cl(sw)
        window.addEventListener('resize', () => {
            sw = window.innerWidth
            cl(sw)
        })

        function cl(sw) {
            if (sw > 768) {
                _this.ispc = true
            } else {
                _this.ispc = false
            }
        }


    },
    watch: {
        $route: {
            handler(route) {
                //this.$emit('update:expup', false)
                let id = Number(route.params.id)
                fetch('https://www.fastmock.site/mock/5f854ec44ed74b246903c6e75806ec14/api/life').then((rs) => {
                    return rs.json()
                }).then((data) => {
                    let fi = data.filter((el) => {
                        return el.id === id
                    })
                    this.info = fi[0]
                })
                //评论系统
                window.livereOption = {
                    refer: 'v1.leeao82.com/#/lifes/'+id
                }
                let j, e = document.getElementsByTagName('script')[0];

                if (typeof LivereTower === 'function') {
                    return;
                }

                j = document.createElement('script');
                j.src = 'https://cdn-city.livere.com/js/embed.dist.js';
                j.async = true;
                e.parentNode.insertBefore(j, e);

            },
            immediate: true
        },
        expup: {
            handler(value) {
                if (value) {
                    document.body.classList.add('dj')
                } else {
                    setTimeout(() => {
                        document.body.classList.remove('dj')
                    }, 400)
                }

            },
            immediate: true
        }
    },
    methods: {
        gb() {
            this.$emit('update:expup', false)
        }
    },
    computed: {}
}
</script>

